
export default {
	onLaunch: function() {
		uni.onNetworkStatusChange(function(res) {
			console.log(res.isConnected);
			uni.showToast({
				title: res.networkType,
				duration: 3000,
				mask: true
			});
			if (!res.isConnected) {
				uni.navigateTo({
					url: 'pages/none/none'
				});
			}
		});
	},
	onShow: function() {
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide');
	}
};
